import { HttpClient } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { ApiRequestService } from './api-request.service';

@Injectable({
  providedIn: 'root'
})
export class PlaceOrderService {

  private currentOrderSubject: BehaviorSubject<any>;
  public currentOrder: Observable<any>;

  private placedOrderStatusSubject: BehaviorSubject<any>;
  public placedOrderStatus: Observable<any>;

  private orderTimeSlotSubject: BehaviorSubject<any>; 
  public orderTimeSlotStatus: Observable<any>;

  constructor(protected apiService: ApiRequestService) {

    let currentOrder = localStorage.getItem('currentOrder');
    this.currentOrderSubject = new BehaviorSubject<any | null>(currentOrder != null ? JSON.parse(currentOrder) : null);
    this.currentOrder = this.currentOrderSubject.asObservable();

    let placedOrder = localStorage.getItem('orderResponseStatus');
    this.placedOrderStatusSubject = new BehaviorSubject<any | null>(placedOrder != null ? JSON.parse(placedOrder) : null);
    this.placedOrderStatus = this.placedOrderStatusSubject.asObservable();

    let selectedTimingSlot = localStorage.getItem('selectedTimingSlot');
    this.orderTimeSlotSubject = new BehaviorSubject<any | null>(selectedTimingSlot != null ? JSON.parse(selectedTimingSlot) : null);
    this.orderTimeSlotStatus = this.orderTimeSlotSubject.asObservable();
  }

  public setCurrentOrder(orderInfo: any): Observable<any> {
    localStorage.setItem('currentOrder', JSON.stringify(orderInfo));
    this.currentOrderSubject.next(orderInfo);
    return of(null);
  }

  public setSelectedTimeSlot(selectedTimingSlot: any) {
    localStorage.setItem('selectedTimingSlot', JSON.stringify(selectedTimingSlot));
    this.orderTimeSlotSubject.next(selectedTimingSlot);
  }

  public get currentOrderValue(): any {
    return this.currentOrderSubject.value;
  }

  // public setOrderResponseStatus(orderInfo: any): Observable<any> {
  //   localStorage.setItem('orderResponseStatus', JSON.stringify(orderInfo));
  //   this.placedOrderStatusSubject.next(orderInfo);
  //   return of(null);
  // }

  public get placedOrderValue(): any {
    return this.placedOrderStatusSubject.value;
  }
}
