import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Cart } from 'src/app/models/cart';
import { environment } from '../../environments/environment';
import { Brand, BrandFilter } from '../models/brand';
import { ApiRequestService } from './api-services/api-request.service';

@Injectable({
    providedIn: 'root'
})
export class CartService {

    private _updateCart: Subject<Cart> = new Subject();
    public cartUpdated: Observable<any> = this._updateCart.asObservable();

    private storage_key: string = "cart_list";

    private cartList: Cart[] = [];

    constructor() {
        this.loadCartList();
    }

    addItem(productId: number) {
        this.loadCartList();

        let cartItem = undefined;
        let index = this.cartList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            cartItem = this.cartList[index];
            cartItem.Quantity += 1;
            this.cartList[index] = cartItem;
        } else {
            cartItem = new Cart();
            cartItem.ProductVariantId = productId;
            cartItem.Quantity = 1;
            this.cartList.push(cartItem);
        }
        this.saveCartList();
        this._updateCart.next(cartItem);
    }

    decreaseItem(productId: number) {
        this.loadCartList();

        let cartItem = undefined;
        let index = this.cartList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            if (this.cartList[index].Quantity > 1) {
                cartItem = this.cartList[index];
                cartItem.Quantity -= 1;
                this.cartList[index] = cartItem;
            } else {
                this.cartList.splice(index, 1);
            }
        }
        this.saveCartList();
        this._updateCart.next(<Cart>cartItem);
    }

    removeItem(productId: number) {
        this.loadCartList();
        let index = this.cartList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            this.cartList.splice(index, 1);
        }
        this.saveCartList();
        this._updateCart.next(null);
    }


    getProductCartCount(productId: number): number {
        this.loadCartList();

        let count = 0;
        let index = this.cartList.findIndex(i => i.ProductVariantId == productId);
        if (index > -1) {
            count = this.cartList[index].Quantity;
        }
        return count;
    }

    getCartCount(): number {
        let sum = 0;
        this.cartList.forEach(item => {
            sum += item.Quantity;
        })
        return sum;
    }

    loadCartList() {
        if (this.cartList.length == 0) {
            this.cartList = <Cart[]>JSON.parse(localStorage.getItem(this.storage_key) ?? "[]");
        }
    }

    clearList() {
        this.cartList = [];
        localStorage.removeItem(this.storage_key);
        this._updateCart.next(null);
    }

    getCartList(): Cart[] {
        return this.cartList;
    }

    saveCartList() {
        localStorage.setItem(this.storage_key, JSON.stringify(this.cartList));
    }
}