import { HttpClient } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { BankAccount } from 'src/app/models/bank-account';
import { OrderFilter } from 'src/app/models/order';
import { environment } from 'src/environments/environment';
import { ApiRequestService } from './api-request.service';

@Injectable({
  providedIn: 'root'
})
export class BankAccountService {

  private add_bank_account_url = environment.apiUrl + "bankaccount/add";
  private get_bank_account_url = environment.apiUrl + "bankaccount/get";
  private delete_bank_account_url = environment.apiUrl + "bankaccount/delete";
  private get_bank_account_list_url = environment.apiUrl + "bankaccount/get-list";

  constructor(protected apiService: ApiRequestService) {
  }

  public addBankAccountInfo(bankAccount: BankAccount): Observable<any> {
    return this.apiService.post(this.add_bank_account_url, bankAccount);
  }

  public getBankAccountInfo(id: Number): Observable<any> {
    return this.apiService.get(this.get_bank_account_url + "/" + id);
  }
  
  public getBankAccountList(): Observable<any> {
    return this.apiService.get(this.get_bank_account_list_url);
  }

  public deleteAccount(id: number): Observable<any> {
    return this.apiService.delete(this.delete_bank_account_url + '/' + id);
  }

}
