import { BaseFiler, ProductQueryType } from "./common";

export class ProductView {
  Id!: number;
  ProductId!: number;
  ProductCode!: string;
  SubCategoryId!: number;
  SubCategoryName?: string;
  BrandName?: string;
  BrandId!: number;
  ProductName!: string;
  Description!: String;
  ProductImages?: String[];
  Mrp!: number;
  Measure!: number;
  Volume!: string;
  StocksAvailable!: number;
  Gst!: number;
  Cost!: number;
  MaxPurchase!: number;
  Available!: number;
  DiscountPercentage!: number;
}

export class Product {
  Id!: number;
  ProductCode!: string;
  SubCategoryId!: number;
  SubCategoryName?: string;
  BrandName?: string;
  BrandId!: number;
  ProductName!: string;
  Description!: String;
  ProductImages: String[] = [];
  ProductVariants: ProductVariant[] = [];
}


export class ProductVariant {
  Id!: number;
  ProductId!: number;
  Mrp!: number;
  Price: number;
  MeasureId: string;
  MeasureName!: string;
  Volume!: string;
  StocksAvailable!: number;
  Gst!: number;
  Cost!: number;
  MaxPurchase!: number;
  Available!: number;
  DiscountPercentage!: number;
  Product!: Product;
}

export class ProductFilter extends BaseFiler{
  Search: string;
  ProductCode?: string;
  Ids?: string;
  SubCategoryId?: number;
  MainCategoryKey?: string;
  CategoryKey?: string;
  SubCategoryKey?: string;
  ProductGroupKey?: string;
  ProductGroupId?: number;
  BrandId?: number;
  Enabled: boolean = true;
  OfferId: number = -1;
  ProductQueryType: ProductQueryType = ProductQueryType.FilterOrNone;

}

