import { HttpClient } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { OrderFilter } from 'src/app/models/order';
import { environment } from 'src/environments/environment';
import { ApiRequestService } from './api-request.service';

@Injectable({
  providedIn: 'root'
})
export class WalletService {

  private get_wallet_url = environment.apiUrl + "wallet/get";
  private get_wallet_list_url = environment.apiUrl + "wallet/get-list";

  constructor(protected apiService: ApiRequestService) {
  }

  public getWalletInfo(id: Number): Observable<any> {
    return this.apiService.get(this.get_wallet_url + "/" + id);
  }
  
  public getWalletList(): Observable<any> {
    return this.apiService.get(this.get_wallet_list_url);
  }
 

}
