import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { DeliveryTiming, DeliveryTimingFilter, WeekDayStatus } from '../../models/delivery-timings';
import { ApiRequestService } from './api-request.service';

@Injectable({
  providedIn: 'root'
})
export class DeliveryTimingsService {

    private deliverytimings_dropdown_url = environment.apiUrl + "/deliverytimings/get-select-list";
    private add_deliverytimings_url = environment.apiUrl + "deliverytimings/add";
    private edit_deliverytimings_url = environment.apiUrl + "deliverytimings/edit";
    private get_deliverytimings_url = environment.apiUrl + "deliverytimings/get";
    private get_deliverytimingslist_url = environment.apiUrl + "deliverytimings/get-list";
    private delete_deliverytimings_url = environment.apiUrl + "deliverytimings/delete";

    constructor(protected apiService: ApiRequestService) {
    }
  
    public getDeliveryTimingssDropdown(): Observable<any> {
      return this.apiService.get(this.deliverytimings_dropdown_url);
    }
  
    public addDeliveryTimingsInfo(weekDayStatus: WeekDayStatus): Observable<any> {
      return this.apiService.post(this.add_deliverytimings_url, weekDayStatus);
    }
  
    public getDeliveryTimingsInfo(id: number): Observable<any> {
      return this.apiService.get(this.get_deliverytimings_url + "/" + id);
    }
  
    public editDeliveryTimingsInfo(weekDayStatus: WeekDayStatus): Observable<any> {
      return this.apiService.put(this.edit_deliverytimings_url, weekDayStatus);
    }
  
    public getDeliveryTimingsList(quantityMeasureFilter: DeliveryTimingFilter): Observable<any> {
      return this.apiService.get(this.get_deliverytimingslist_url, quantityMeasureFilter);
    }
  
    public deleteDeliveryTimings(quantityMeasureId: number): Observable<any> {
      return this.apiService.delete(this.delete_deliverytimings_url + "/" + quantityMeasureId);
    }
}