import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../environments/environment';
import { Address, AddressFilter } from '../../models/address';
import { ApiRequestService } from './api-request.service';

@Injectable({
  providedIn: 'root'
})
export class AddressesService {

  private address_dropdown_url = environment.apiUrl + "/address/get-select-list";
  private add_address_url = environment.apiUrl + "address/add";
  private edit_address_url = environment.apiUrl + "address/edit";
  private get_address_url = environment.apiUrl + "address/get";
  private get_addresses_url = environment.apiUrl + "address/get-list";
  private delete_address_url = environment.apiUrl + "address/delete";

  constructor(protected apiService: ApiRequestService) {
  }

  public getAddressesDropdown(): Observable<any> {
    return this.apiService.get(this.address_dropdown_url);
  }

  public addAddressInfo(addressInfo: Address): Observable<any> {
    return this.apiService.post(this.add_address_url, addressInfo);
  }

  public getAddressInfo(id: number): Observable<any> {
    return this.apiService.get(this.get_address_url + "/" + id);
  }

  public editAddressInfo(addressInfo: Address): Observable<any> {
    return this.apiService.put(this.edit_address_url, addressInfo);
  }

  public getAddressList(addressFilter: AddressFilter): Observable<any> {
    return this.apiService.get(this.get_addresses_url, addressFilter);
  }

  public deleteAddress(addressId: number): Observable<any> {
    return this.apiService.delete(this.delete_address_url + "/" + addressId);
  }
}
