import { Address } from "./address";
import { PaymentType } from "./common";

export class Order {
    
    Id: number;
    UserId: string;
    TimePeriod: number;
    RelDay: number;
    IsDelivery: boolean;
    OrderDate: Date;
    OrderState: number;
    DispatchedDate: Date;
    DeliveryDate: Date;
    DeliveredDate: Date;
    TimePeriodText: string;
    Purchases: any[];
    PhoneNumber: number;
    SumAmount: number;
    Gst: number;
    TotalBill: number;
    Enabled: boolean;
    Address: Address;
    PaymentMode: PaymentType;
    PaymentId: string;
    PaymentOrderId: string;
    PaymentSignature: string;
    UseWallet: Boolean;
}

export class OrderDetail { 
    OrderId: number;
    Id: number;
    ReturnDetailId!: number;
    ProductVariantId: number;
    ProductName: string;
    UnitType: any;
    ProductImage: any;
    TotalQuantity: number;
    OrderedQuantity: number;
    ReturnTotalSum: number;
    ReturnOrderDate: number;
    ReturnedQuantity: number;
    TotalReturnedQuantity: number;
    Discount: number;
    Price: number;
    DiscPrice: number;
    Gst: number;
    Total: number;
    Volume: number;
    OrderedTotal: number;
    ReturnTotal: number;
}

export enum OrderStatus
{
    All = 0,
    Placed = 1,
    Confirmed = 2,
    Packed = 3,
    Dispatched = 4,
    Delivered = 5,
    Cancelled = 6,
    Rejected = 7
}
  
